@media (min-width: 1025px) {
    main {
        padding: 0 50px;
    }

    .advantages {
        .container-fluid {
            .swiper.swiper-advantages {
                max-width: 1142px;
                margin: 0 auto;
            
                .swiper-wrapper {
                    flex-wrap: wrap;
                    row-gap: 26px;
                    column-gap: 30px;
                    justify-content: center;
    
                    .swiper-slide {
                        min-width: 263px;
                    }
                }
            }
        }
    }

    .delivery, .services {
        .swiper {
            max-width: 932px;
            padding-left: 91px;
            padding-right: 91px;
            position: relative;

            &::before,
            &::after {
                content: "";
                position: absolute;
                width: 96px;
                height: 100%;
                background-color: $primary-bg-color;
                left: -15px;
                top: 0;
                z-index: 2;
            }
            &::after {
                left: inherit;
                right: -15px;
                z-index: 2;
            }
        }
    }
    .services .swiper::before,
    .services .swiper::after {
        background-color: $secondary-bg-color;
    }
}
