.content-section {
    margin-top: 10px;
    .first-screen {
        .swiper {
            overflow: hidden;
            height: 700px;
            border-radius: 20px;

            .slide-1 {
                background: url("assets/img/slide-1-bg.svg") center no-repeat;
            }
            .slide-2 {
                background: url("assets/img/slide-2-bg.svg") center no-repeat;
            }
            .slide-3 {
                background: url("assets/img/slide-3-bg.svg") center no-repeat;
            }
            .slide-4 {
                background: url("assets/img/slide-4-bg.svg") center no-repeat;
            }

            .swiper-slide {
                border-radius: 20px;
                display: flex;
                align-items: center;
                background-size: cover;

                .wraper {
                    width: 100%;
                    max-width: 1340px;
                    padding: 0 100px;
                    padding-bottom: 60px;
                    margin: 0 auto;

                    h1 {
                        max-width: 463px;
                        width: 100%;
                        margin-bottom: 8px;
                        text-transform: uppercase;
                    }

                    p {
                        max-width: 320px;
                        font-size: 20px;
                        font-weight: 300;
                        line-height: 27.1px;
                        margin-bottom: 37px;
                    }

                    .btn-arrow {
                        height: 68px;
                    }
                }


            }

            .wrap {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                max-width: 1623px;
                z-index: 1;
            }

            .swiper-pagination {
                bottom: 43px;
            }
        }
    }
}